@import url('https://fonts.googleapis.com/css?family=Muli');
*{
    font-family: 'Muli', sans-serif;
}

.jumbotron .lead{
    color: #000;
}

.card-wrapper{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.card{
    font-family: 'Muli';
    padding-top: 8px;
    -webkit-box-shadow: 12px 15px 20px rgba(0, 0, 0, .1);
            box-shadow: 12px 15px 20px rgba(0, 0, 0, .1);
    text-align: center;
    width: 22em;
    background: -webkit-gradient(linear, left top, left bottom, color-stop(50%, #fff), color-stop(50%, #000));
    background: -webkit-linear-gradient(top, #fff 50%, #000 50%);
    background: -o-linear-gradient(top, #fff 50%, #000 50%);
    background: linear-gradient(to bottom, #fff 50%, #000 50%);
    border-color: #000;
    background-size: 100% 200%;
    -webkit-transition: background-position .4s;
    -o-transition: background-position .4s;
    transition: background-position .4s;
}

.card:hover{ 
    cursor: pointer;
    background-position: 0 -100%;
    color: #fff;
    border-color: #fff;
}


.lead{
    font-family: 'Muli';
}

.btn{
    background-color: #222;
    color: #fff;
    border-color: #222;
}

.btn:hover{
    background-color: #fff;
    color: #222;
    border-color: #222;
}

@media screen and (max-width: 1040px){
    :root{
        overflow: scroll;
    } 

    .card-wrapper{
        width: 100vw;
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-align: center;
            align-items: center;
    }

    .card{
        width: 70vw;
        margin-top: 5vh;
    }
}

@media screen and (max-width: 520px){
    .card{
        width: 90vw;
    }
}


